* {
  margin: 0;
}

body{
  background-color: whitesmoke;
}

.app {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.app__header {
  display: flex;
  align-items: center;
  margin: 20px;
  justify-content: space-between;

}

.app__left {
  flex: 0.9;
}

.app__stats {
  display: flex;
  justify-content: space-evenly;
}

@media (max-width: 990px){
  .app {
    flex-direction: column;
  }
}